.ticket {
  border: 2px solid black;
  height: 600px;
  width: 400px;
  margin: 20px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  &__buttons{
    display: flex;
  
  }
  &__counter{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 70%;
  }
}